body {
  margin: 0px;
  font-family: 'Titillium Web', sans-serif;
  color: black;
}

.container {
  display: grid;
  grid-template-columns: 1fr;
}

textarea {
  border: 1px solid #26bfd4;
  background-color: transparent;
  font-size: 1rem;
  height: 100px;
  outline: none;
  padding: 10px;
}

.select-element {
  border: 1px solid #ccc;
  font-size: 0.8rem;
  width: 100%;
  border-radius: 3px;
  overflow: hidden;
  background-color: transparent;
  outline: none;
}

.btn {
  cursor: pointer;
  height: 42px;
  font-size: 1em;
  font-weight: 500;
  border: 1px solid transparent;
  transition: all 0.5s ease-in-out;
  background-color: #207b88;
  color: white;
}

.btn:active, .btn:focus {
  outline: none;
}

.btn:hover {
  background-color: #171727;
}

.one-column {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr;
  color: #3f3939;
}

.one-column .one-color {
  color: #4CAF50 !important;
}

.two-column {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr 1fr;
  color: #3f3939;
}

.three-column {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr 1fr 1fr;
  color: #3f3939;
}

.nav-wrapper {
  background-color: '#00000000';
  display: flex;
  justify-content: space-between;
  padding: 30px;
  font-family: 'Titillium Web', sans-serif;
}

.nav-wrapper .left-side {
  display: flex;
}

.nav-wrapper .right-side {
  display: grid;
  opacity: 100;
  grid-template-columns: 1fr 1fr;
}

.nav-wrapper .right-side .linko {
  text-decoration-color: #d4a669;
}

.nav-wrapper .right-side .active {
  color: orange;
  font-size: 1rem;
  line-height: 1.6rem;
  text-decoration: none;
}

.nav-wrapper .nav-link-wrapper {
  height: 22px;
  border-bottom: 1px solid transparent;
  transition: 0.5s ease-in-out;
  margin-right: 20px;
  text-transform: uppercase;
  font-size: 0.9rem;
}

.nav-wrapper .nav-link-wrapper .nav-link-active {
  border-bottom: 1px solid black;
  border-spacing: 50px;
  text-decoration: none;
  color: #008dff;
}

.nav-wrapper .nav-link-wrapper a {
  color: orange;
  text-decoration: none;
}

.nav-wrapper .nav-link-wrapper a:hover {
  color: #d3cbcb;
}

.nav-wrapper .nav-link-wrapper:hover {
  border-bottom: 1px solid #e7e7e7;
}

.nav-wrapper .nav-link-wrapper .nav-link-active {
  border-bottom: 1px solid #e7e7e7;
  color: #e7e7e7;
}

.verticalspace {
  margin: 20px;
}

.portsearch {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transition: 1s ease-in-out;
  color: transparent;
}

.search-outer {
  margin-bottom: 3px;
}

.search-outer ::-webkit-input-placeholder {
  color: #f1ecec !important;
}

.search-outer input[type=text] {
  padding: 10px;
  font-size: 15px;
  border: 1px solid grey;
  float: left;
  height: 40px;
  width: 27%;
  background: transparent;
}

.search-outer button {
  float: left;
  height: 40px;
  width: 4%;
  padding: 10px;
  background: #2196F3;
  color: white;
  font-size: 17px;
  border: 1px solid grey;
  border-left: none;
  border-radius: 6px;
  cursor: pointer;
}

.search-outer button:hover {
  background: #0b7dda;
}

.search-outer ::after {
  content: "";
  clear: both;
  display: table;
}

.portfolio-items-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.portfolio-items-wrapper .portfolio-item-wrapper {
  position: relative;
}

.portfolio-items-wrapper .portfolio-item-wrapper .portfolio-img-background {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 350px;
  width: 100%;
}

.portfolio-items-wrapper .portfolio-item-wrapper .img-text-wrapper {
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  text-align: center;
  padding-left: 100px;
  padding-right: 100px;
}

.portfolio-items-wrapper .portfolio-item-wrapper .img-text-wrapper .subtitle {
  transition: 1s ease-in-out;
  color: transparent;
}

.portfolio-items-wrapper .portfolio-item-wrapper .img-text-wrapper:hover .subtitle {
  color: #26bfd4;
  font-weight: 400;
}

.portfolio-items-wrapper .portfolio-item-wrapper .logo-wrapper img {
  width: 30%;
  margin-bottom: 20px;
}

.image-blur {
  transition: 1s;
  filter: brightness(10%);
}

.Portfolio-manager-wrapper {
  display: grid;
  grid-template-columns: 3fr 1fr;
}

.Portfolio-manager-wrapper .left-column {
  background-color: #757373;
}

.Portfolio-manager-wrapper .right-column {
  background-color: #42454a;
}

.portfolio-form-wrapper {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr;
  padding: 42px;
  color: #3f3939;
}

.portfolio-form-wrapper input {
  font-family: "Titillium Web", sans-serif;
  color: #42454a;
  padding: 5px 0px;
  width: 100%;
  border-top: 0px;
  border-right: 0px;
  border-left: 0px;
  border-bottom: 1px solid #26bfd4;
  background-color: transparent;
  transition: 0.5s all ease-in-out;
  margin-bottom: 21px;
}

.portfolio-form-wrapper input:focus {
  outline: none;
  border-bottom: 1px solid #207b88;
}

.portfolio-form-wrapper input {
  margin-bottom: 0px;
}

.portfolio-form-wrapper .image-uploaders {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 200px 200px 200px;
  color: #3f3939;
}

.portfolio-sidebar-list-wrapper .portfolio-item-thumb {
  padding: 21px;
}

.portfolio-sidebar-list-wrapper .portfolio-item-thumb .portfolio-thumb-img img {
  width: 100%;
}

.portfolio-sidebar-list-wrapper .portfolio-item-thumb .title {
  color: #757373;
}

.fbstyle button {
  height: 50px;
  width: 300px;
  font-size: 15px;
  font-family: 'Titillium Web', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about {
  color: #1a0000;
  font-weight: bold;
  font-size: 18px;
  font-family: 'Titillium Web', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: auto;
}

.aboutgreen {
  color: #5bc25b;
  font-weight: bold;
  font-size: 18px;
  font-family: 'Titillium Web', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: auto;
}

.ggstyle button {
  height: 50px;
  width: 300px;
  font-size: 50px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gitstyle button {
  height: 50px;
  width: 300px;
  font-size: 16px;
  color: #f8efef;
  background-color: #201c1c;
  font-family: 'Titillium Web', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
}

.fby {
  display: flex;
  justify-content: center;
  align-items: center;
}

.fby .fbyg {
  display: grid;
  padding-top: 40px;
  grid-template-columns: 1fr;
  grid-auto-rows: 65px;
  align-items: center;
}

.fby .fbyg .fbbtn {
  display: flex;
  align-items: center;
  justify-content: center;
}

.fby .fbyg .ggbtn {
  display: flex;
  align-items: center;
  justify-content: center;
}

.fby .fbyg .gitbtn {
  display: flex;
  align-items: center;
  justify-content: center;
  color: blue;
}

.auth-page-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: calc(100vh - 84px);
}

.auth-page-wrapper .left-column {
  background-size: cover;
}

.auth-page-wrapper .right-column {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 60px;
  background-color: #757373;
}

.auth-page-wrapper .right-column .loggers {
  display: grid;
  grid-template-columns: 1fr;
}

.auth-page-wrapper .right-column .loggers .emailer {
  display: flex;
  justify-content: center;
}

.auth-page-wrapper .right-column .loggers .hidder {
  display: flex;
  justify-content: center;
}

.auth-page-wrapper .right-column .loggers .face {
  display: flex;
  justify-content: center;
}

.socials {
  display: flex;
  justify-content: center;
  align-items: center;
}

.socials .social-grid {
  display: grid;
  grid-template-rows: 1fr 1fr;
}

.but {
  cursor: pointer;
  height: 42px;
  font-size: 1em;
  font-weight: 500;
  border: 1px solid transparent;
  transition: all 0.5s ease-in-out;
  background-color: #207b88;
  color: #cfb6b6;
}

.martha {
  opacity: 0;
}

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: url(https://i.pinimg.com/originals/6d/72/ec/6d72ec208bf85647d5e594db79218802.jpg) center center;
  background-attachment: fixed;
  background-size: cover;
  color: #cbfacd !important;
  font-family: "Open Sans";
  height: 100%;
  width: 100%;
}

body input,
body textarea {
  border: 1px solid transparent;
  border-radius: 3px;
}

#root .container {
  margin: 0 auto;
  max-width: 1000px;
}

#root .search-outer {
  margin: 50px 0 100px;
  text-align: left;
}

#root .search-outer .searchform {
  display: inline-block;
  position: relative;
  left: 0px;
  right: 0;
}

#root .search-outer .searchform input {
  outline: medium none;
}

#root .search-outer .searchform input[type="search"] {
  background-color: transparent;
  border: medium none;
  border-width: 0 0 1px 0;
  border-style: solid;
  border-color: #dadbdb;
  border-radius: 0;
  box-sizing: content-box;
  color: #ebd6d6;
  cursor: pointer;
  font-family: inherit;
  font-size: 100%;
  margin-bottom: 0;
  padding: 3px 0;
  transition: all 0.5s ease 0s;
  max-width: 300px;
  width: calc(100% - 15px);
  -webkit-appearance: none;
  -moz-appearance: none;
  /* clears the 'X' from Internet Explorer */
  /* clears the 'X' */
}

#root .search-outer .searchform input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

#root .search-outer .searchform input[type="search"]::-ms-clear {
  display: none;
  width: 0;
  height: 0;
}

#root .search-outer .searchform input[type="search"]::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

#root .search-outer .searchform input[type="search"]::-webkit-search-decoration, #root .search-outer .searchform input[type="search"]::-webkit-search-cancel-button, #root .search-outer .searchform input[type="search"]::-webkit-search-results-button, #root .search-outer .searchform input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

#root .search-outer .searchform input[type="search"]:focus {
  background-color: transparent;
  color: #fff;
  cursor: auto;
  padding: 3px 0;
}

#root .search-outer .searchform:hover input[type="search"] {
  background-color: transparent;
  color: #ebd6d6;
  cursor: auto;
  padding: 3px 0;
}

#root .search-outer button[type="submit"] {
  background-color: rgba(0, 0, 0, 0);
  border: medium none;
  color: #fff;
  cursor: pointer;
  height: 30px;
  line-height: 1;
  float: right;
  font-size: 1em;
  pointer-events: none;
  width: auto;
  z-index: 1000000000;
}

#root .search-outer .fa-search {
  padding-left: 0;
  padding-right: 0;
}

::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: rgba(255, 255, 255, 0.5);
}

::-moz-placeholder {
  /* Firefox 19+ */
  color: rgba(255, 255, 255, 0.5);
}

:-ms-input-placeholder {
  /* IE 10+ */
  color: rgba(255, 255, 255, 0.5);
}

:-moz-placeholder {
  /* Firefox 18- */
  color: rgba(255, 255, 255, 0.5);
}

.data-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
  padding: 0 15px;
}

.data-list:after {
  content: "";
}

.data-list:after,
.data-list li {
  width: calc(33.33% - 10px);
}

.data-list li {
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 15px;
  padding: 30px;
}

.data-list li .title {
  color: #ebd6d6;
  margin-bottom: 30px;
  text-decoration: none;
}

.data-list li .link {
  color: #ebd6d6;
  text-decoration: none;
}

@media only screen and (max-width: 768px) {
  .data-list:after,
  .data-list li {
    width: calc(50% - 8px);
  }
}

@media only screen and (max-width: 400px) {
  .data-list:after,
  .data-list li {
    width: 100%;
  }
}

